/* ==========================================================================
   Browse Happy prompt
   ========================================================================== */

.browsehappy {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

html{
	min-height:100%;
	background: $mx-black;
}

body{
	min-height: initial !important;
	padding-top: 45px;
	font-family: 'Open Sans';
	background-color: #ffffff;
}

.growl{
	z-index:10;
}

a {
	text-decoration: none;
}

.doc-info {
  .doc-poll {
    padding-bottom: 20px;
  }
  a.btn {
    background-image: none;
    border: none;
    color: white;
    &.doc-support {
      @include btn-3d($mx-blue, $mx-dark-blue);
    }
    &.doc-oppose {
      @include btn-3d($mx-green, $mx-light-green);
    }
  }
}

p{
	&.gray{
		color:$light-gray;
	}

}
del, ins{
	@include single-transition(all, .2s, linear);
}

del{
	color:red;
}
ins{
	color:green;
}

input{
	&.disabled{
		cursor:default;
		opacity:.5;
	}
}

select, textarea, input[type="text"], input[type="password"], input[type="datetime"], input[type="datetime-local"], input[type="date"], input[type="month"], input[type="time"], input[type="week"], input[type="number"], input[type="email"], input[type="url"], input[type="search"], input[type="tel"], input[type="color"], .uneditable-input{
	margin-top:10px;
}

textarea.form-inline{
	width:100%;
	margin-top:0;
	height:150px;
}

.btn-black{
	@include grad-bg($black-grad-start, $black-grad-end);

	color:$light-gray;
}

/* Glyphicons */

.glyphicon.glyphicon-thumbs-up, .glyphicon.glyphicon-thumbs-down, .glyphicon.glyphicon-flag{
	cursor: pointer;
}

/* GLOBAL SEARCH
------------------------------ */
.global-search {

	form {
		width: 100%;
		position: relative;
		top: 14px; /* TO-DO: Needs to be dynamic */
	}

	.input-search {
		margin: 0;
	}

	.input-submit {
		height: 24px;
		width: 24px;
		margin: -12px 0 0;
		border: 0;
		position: absolute;
		right: 5px;
		top: 50%;
		text-indent: -9999px;
		background: transparent url(../img/dark-blue-search.png) center no-repeat;
	}
}

/* CONTENT
------------------------------ */
#main{
	padding-right:0;
	padding-left:0;
	position:relative;
}

.navbar-default{
	.navbar-nav{
		li{
			a{
				&.black{
					color:black;

					&:hover{
						color:white;
					}
				}
			}
		}
	}
}

.content-container{
	// @include single-box-shadow(black, 1em, 1em, 1em, -1em);
	background:white;
	@include box-sizing(border-box);
	border: 1px solid #f0f0f0;
	@include border-radius(4px);
	padding: 10px 15px;

	a{
		color:$blue-grad-end;
	}
	ul{
		list-style:none;
	}
}

.content{
	@extend .content-container;
	padding: 30px;

}

.content.col-md-12 .row{
	h1{
		margin-bottom: 45px;
	}
	ul{
		list-style: none;
		margin-top: 40px;
		padding: 0px 0px;
		@include box-sizing(border-box);
		li{
			border-top: 1px solid #ccc;
			padding: 12px 10px;
			position: relative;
			@include box-sizing(border-box);
			a{
				font-weight: bold;
				font-size:1.2em;
				color: #444;
				line-height: 140%;
				&:hover{
					color: #111;
					text-decoration: underline;
				}
			}
			.list-doc-info{
				color: #999;
				font-size: .9em;
				margin-top: 4px;
				span{
					//border-right: 2px solid #cccccc;
					padding-right: 10px;
				}
				.doc-created-date{

				}
				.dov-updated-date{

				}
				.doc-action-count{
					background: #999;
					color: #FFF;
					text-align: center;
					font-weight: bold;
					@include border-radius(2px);
					position: absolute;
					right:0;
					top: 12px;
					display: block;
					padding: 3px 8px 4px 8px;
				}
			}
			.doc-categories{
				padding-right: 0px;
				margin-right: 0px;
				position: relative;
				top: -2px;
			}
			.category{
				background: #999;
				color: #FFF;
				@include border-radius(2px);
				text-align: center;
				font-weight: bold;
				padding: 3px 6px 4px 6px;
				font-size: .8em;
				margin-left: 4px;
				display:inline-block;
			}
			.doc-statuses{
				position: relative;
				top: -2px;
				.status{
					background: #489EF9;
					color: #FFF;
					@include border-radius(2px);
					text-align: center;
					font-weight: bold;
					padding: 3px 6px 4px 6px;
					font-size: .8em;
					margin-right: 4px;
					display:inline-block;
				}
			}


		}
	}
}

.home-select2-container{
	.select2-container{
		margin-top:10px;
		height:34px;

		.select2-choice{
			height:34px;
			line-height:33px;
		}
	}
}

.select2-container{
	width:100%;
	margin-top: 5px;
}

.user-info h1{
	margin-bottom: 5px !important;
}

.user-gravatar{
	display: inline-block;
	margin-right:10px;
	margin-left: 20px;
