.content.col-md-12 .row ul.user-sponsored-docs{
	margin-top: 0px;
	li{
		border-top: none;
		border-bottom: 1px solid #ccc;
	}
}

.content.col-md-12 .row ul.user-activity-items{
	margin-top: 0px;
	li{
		border-top: none;
		border-bottom: 1px solid #ccc;
		padding-top: 20px;
		padding-bottom: 20px;
		blockquote{
			margin-bottom: 5px;
			background: #DEEEFF;
			border: none;
			padding: 10px 15px;
			margin-top: 5px;
			@include border-radius(4px);
		}
		a{
			font-size: 1em !important;
			text-decoration: underline !important;
		}
		.user-activity-title{
			font-size: 1em !important;

		}
	}
}



