.bookmark{
	position:absolute;
	top:0;
	right:0;

	.bookmark-label{
		position:relative;
		top:20px;
		right:40px;
		font-size:.8em;
	}
}

//CSS Graphics
	//Ribbon
	.ribbon{
		width:25px;
		height:40px;
		overflow:hidden;
		position:relative;
		top:-20px;
		right:-15px;

		.ribbon-container{
			position:relative;
			width:25px;
			height:40px;
			overflow:hidden;
			margin:0 auto;
		}

		.ribbon-base{
			height:25px;
			width:25px;
			background:#999;
			position:relative;
			z-index:2;

			&:after{
				content:' ';
				position:absolute;
				top:0;
				width:19px;
				left:3px;
				height:36px;
				border-left:1px dashed #cacaca;
				border-right:1px dashed #cacaca;
			}
		}

		.ribbon-left-corner{
			@include transform(45deg);

			width:25px;
			height:25px;
			background:#999;
			position:absolute;
			bottom:5px;
			left:-13px;
			z-index:1;
		}

		.ribbon-right-corner{
			@include transform(45deg);

			width:25px;
			height:25px;
			background:#999;
			position:absolute;
			bottom:5px;
			right:-13px;
			z-index:1;
		}

	}

